import { useCallback, useState } from "react"
import styled from "styled-components"
import axios from "axios"
import { useNavigate } from "react-router-dom"
import { propsType } from "../../components/signup/propsType"
import { useCookies } from "react-cookie"

const SignUp = ({memberId, password, name, email, phone, address, extraAddress, checked, birth }: propsType) => {
  const navigate = useNavigate();
  const [cookies, setCookie, removeCookie] = useCookies();
  
  const doSignUp = async (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    try {
      const { data } = await axios.post(
        'http://localhost:4444/register',
        {
          memberId: memberId,
          password: password,
          name: name,
          email: email,
          mobileNumber: phone,
          address: address,
          extraAddress: extraAddress,
          gender: checked,
          birth: birth,
        }
      )
      setCookie('accessToken', data['accessToken'], { path: '/' })
      console.log(data)
      window.alert('  !')
      navigate('/login');
    } catch (error) {
      console.log(error)
    }
  }
  // const onSubmit = useCallback(
  //   async (e: React.MouseEvent<HTMLButtonElement>) => {
  //     e.preventDefault()
  //     try {
  //       console.log("hi")
  //       await axios
  //         .post('/signup', {
  //           memberId: memberId,
  //           password: password,
  //           name: name,
  //           email: email,
  //           mobileNumber: phone,
  //           address: address,
  //           extraAddress: extraAddress,
  //           gender: checked,
  //           birth: birth,
  //         })
  //         .then((res) => {
  //           console.log('response:', res)
  //           if (res.status === 200) {
  //             navigate('./signup/success');
  //           }
  //         })
  //     } catch (err) {
  //       console.error(err)
  //     }
  //   },[]
    // [memberId, password, name, email, phone, address, extraAddress, checked, birth]
 
  return (
    <SignUpButton type="submit" onClick={doSignUp}>
      <span></span>
    </SignUpButton>
  )

}

export default SignUp

const SignUpButton = styled.button`
  display: block;
  padding: 0px 10px;
  text-align: center;
  overflow: hidden;
  width: 240px;
  height: 56px;
  border-radius: 3px;
  color: rgb(255, 255, 255);
  background-color: rgb(95, 0, 128);
  border: 0px none;
  span {
    display: inline-block;
    font-size: 16px;
    font-weight: 500;
  }
`